<template>
 <main  class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
        <router-link class="breadcrumbs__link" :to="{name:'main'}">
          Каталог
        </router-link>
      </li>
      <li class="breadcrumbs__item">
        <router-link class="breadcrumbs__link" :to="{name:'cart'}">
          Корзина
        </router-link>
      </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{this.orderInfo ? this.orderInfo.id : id}}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
               {{orderInfo?.name}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
               {{orderInfo?.address}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{orderInfo?.phone}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{orderInfo?.email}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                {{paymentMethod}}
              </span>
            </li>
            <li class="dictionary__item" v-show="orderInfo?.comment">
              <span class="dictionary__key">
                Комментарий к заказу
              </span>
              <span class="dictionary__value">
                {{orderInfo?.comment}}
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders" >
            <li class="cart__order" v-for="item in orderInfo?.basket.items" :key="item.productId" :item="item">
              <h3>{{ item.product.title}}</h3>
              <b>{{item.quantity*item.product.price | numberFormat}} ₽</b>
              <span>Артикул: {{item.product.id}}</span>
            </li>

          </ul>

          <div class="cart__total">
            <p>Доставка: <b>{{this.$route.params.deliveryPrice}} ₽</b></p>
            <p>Итого: <b>{{itemsCount}}</b> товара на сумму <b>{{totalPriceWithDelivery | numberFormat}} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>

</template>


<script>
import numberFormat from '@/helpers/numberFormat';


export default {
  filters: { numberFormat },
  props: ['id'],


  created(){
    if(this.$store.state.orderInfo && this.$store.state.orderInfo.id===this.$route.params.id){
      return

    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id )
  },
  computed: {
    orderInfo() {  return this.$store.state.orderInfo;  },

    // вывод общей суммы
    totalPriceWithDelivery() {
      return this.orderInfo?.totalPrice //+ + this.$route.params.deliveryPrice ;//не могу передать Доставку

    },
    // вывод вариантов способа оплаты
    paymentMethod() {
      return `${this.$route.params.paymentMethod === 'card' ? 'картой' : 'наличными'} при получении`;
    },



      // вывод количества товара
    itemsCount() {
    return this.orderInfo?.basket.items.reduce((acc, item) => acc + item.quantity, 0);
    },
  }
}
</script>
