<template>

  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>
    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :colors-id.sync="filterColorId"/>
      <section class="catalog">
        <div v-if="productsLoading" class="preloader">
          <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" viewBox="0 0 50 50">
            <path fill="#9eff00" d="M25,5A20.14,20.14,0,0,1,45,22.88a2.51,2.51,0,0,0,2.49,2.26h0A2.52,2.52,0,0,0,50,22.33a25.14,25.14,0,0,0-50,0,2.52,2.52,0,0,0,2.5,2.81h0A2.51,2.51,0,0,0,5,22.88,20.14,20.14,0,0,1,25,5Z">
              <animateTransform attributeName="transform" type="rotate" from="0 25 25" to="360 25 25" dur="0.5s" repeatCount="indefinite"/>
            </path>
         </svg>
       </div>
        <div v-if="productsLoadingFailed">Призашла ошибка при загрузке товаров <button @click.prevent="loadProducts">Попробовать еще раз</button></div>
        <ProductList :products="products"/>
        <BasePagination v-model="page" :count="countProducts" :pre-page="productsPerPage"/>
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from "@/components/ProductList.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import axios from 'axios';
import { API_BASE_URL } from "@/confing.js";


export default {
    components: { ProductList, BasePagination, ProductFilter },
    data() {
      return {
        filterPriceFrom: 0,
        filterPriceTo: 0,
        filterCategoryId: 0,
        filterColorId: 0,
        page: 1,
        productsPerPage: 6,
        productData: null,
        productsLoading: false,
        productsLoadingFailed: false,
      }
    },
    computed:{
      products(){
         return this.productData ?
         this.productData.items.map(product => {
          return{
            ...product,
            image: product.image.file.url,
          }
         }) : []
      },
      countProducts() { return this.productData ? this.productData.pagination.total : 0 },
    },
    methods:{
      loadProducts(){
        this.productsLoading = true
        this.productsLoadingFailed = false
        clearTimeout(this.loadProductsTime)


        this.loadProductsTime = setTimeout(() =>{
            axios.get(API_BASE_URL+`/api/products`,{
              params: {
                  page: this.page,
                  limit: this.productsPerPage,
                  categoryId: this.filterCategoryId,
                  colorId: this.filterColorId,
                  minPrice: this.filterPriceFrom,
                  maxPrice: this.filterPriceTo,
              }
          })
          .then(response => this.productData = response.data)
          .catch( () => this.productsLoadingFailed = true)
          .then( () => this.productsLoading = false)
        }, 1000)
    },
  },
    watch: {
      page() { this.loadProducts()},
      filterPriceFrom() {  this.loadProducts()},
      filterPriceTo() { this.loadProducts()},
      filterCategoryId() { this.loadProducts()},
      filterColorId() { this.loadProducts()},
    },
    created(){
      this.loadProducts();
    }
}
</script>
