<template>
   <main class="content container">
    <div v-if="productsLoading" class="preloader">
      <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" viewBox="0 0 50 50">
        <path fill="#9eff00" d="M25,5A20.14,20.14,0,0,1,45,22.88a2.51,2.51,0,0,0,2.49,2.26h0A2.52,2.52,0,0,0,50,22.33a25.14,25.14,0,0,0-50,0,2.52,2.52,0,0,0,2.5,2.81h0A2.51,2.51,0,0,0,5,22.88,20.14,20.14,0,0,1,25,5Z">
          <animateTransform attributeName="transform" type="rotate" from="0 25 25" to="360 25 25" dur="0.5s" repeatCount="indefinite"/>
        </path>
      </svg>
    </div>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'cart'}">
            Корзина
          </router-link >
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{$store.state.cartProducts.length}} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST"  @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText v-model="formData.name" :error="formErorr.name"  title="ФИО" placeholder="Введите ваше полное имя"/>
            <BaseFormText v-model="formData.address" :error="formErorr.address"  title="Адрес доставки" placeholder="Введите ваш адрес"/>
            <BaseFormText v-model="formData.phone" :error="formErorr.phone" type="tel" title="Телефон" placeholder="Введите ваш телефон"/>
            <BaseFormText v-model="formData.email" :error="formErorr.email" type="email" title="Email" placeholder="Введи ваш Email"/>
            <BaseFormTextarea v-model="formData.comment" :error="formErorr.comment" title="Комментарий к заказу" placeholder="Ваши пожелания"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input v-model.number="courierDelivery" class="options__radio sr-only" type="radio" name="delivery" value="0"  />
                  <span class="options__value"> Самовывоз <b>бесплатно</b> </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input v-model.number="courierDelivery" class="options__radio sr-only" type="radio" name="delivery" value="500" />
                  <span class="options__value"> Курьером <b>500 ₽</b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input  v-model="paymentMethod" class="options__radio sr-only" type="radio" name="pay" value="card" />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input v-model="paymentMethod" class="options__radio sr-only" type="radio" name="pay" value="cash" />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItem v-for="item in products" :key="item.productId" :item="item" />
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>{{courierDelivery}} ₽</b></p>
            <p>Итого: <b>{{$store.state.cartProducts.reduce((acc, item) => acc + item.amount, 0)}} </b> товара на сумму <b>{{totalPriceWithDelivery | numberFormat}}₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit"> Оформить заказ </button >

        </div>
        <div class="cart__error form__error-block" v-if="formErorrMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
           {{formErorrMessage}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
  import router from '@/router';
  import BaseFormText from '@/components//BaseFormText.vue'
  import BaseFormTextarea from '@/components//BaseFormTextarea.vue'
  import OrderItem from '@/components/OrderItem.vue'
  import axios from "axios";
  import { API_BASE_URL } from "@/confing";
  import numberFormat from '@/helpers/numberFormat';
  import { mapGetters } from "vuex";


  export default {
    filters: { numberFormat },

    data(){
      return{
          formData: {},
          formErorr:{},
          formErorrMessage: '',
          paymentMethod: 'card',
          courierDelivery: 0,
          productsLoading: false,
      }
    },
    computed:{
      ...mapGetters( {products: 'cardDetailProducts', totalPrice: 'cardTotalPrice'}),
      totalPriceWithDelivery() {  return this.totalPrice + +this.courierDelivery; },
  },

  methods:{
    order() {
      this.productsLoading= true,
      this.formErorr = {}
      this.formErorrMessage = ''

          axios.post(API_BASE_URL + `/api/orders`,
            { ...this.formData },
            { params:{userAccessKey: this.$store.state.userAccessKey }
          })
          .then(response =>{
            this.$store.commit('resetCart')
            this.$store.commit('updateOrderInfo', response.data)
            this.$router.push({name: 'orderInfo', params:{id: response.data.id, deliveryPrice: this.courierDelivery, paymentMethod: this.paymentMethod }})
            this.productsLoading = false
          })
          .catch( error => {
            this.formErorr = error.response.data.error.request || {}
            this.formErorrMessage =error.response.data.error.message
            this.productsLoading = false
          })

    }
  },

  created() {
    this.$route.params.paymentMethod === 'card'
  },
  itemsCount() {
    return this.$route.orderInfo.basket.items.reduce((acc, item) => acc + item.quantity, 0);
  },
    // eslint-disable-next-line vue/no-unused-components
    components: { router, BaseFormText, BaseFormTextarea, OrderItem }
  }
</script>
