<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a href="#" class="pagination__link pagination__link--arrow pagination__link--disabled" aria-label="Предыдущая страница" @click.prevent="prev(page)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a class="pagination__link" :class="{'pagination__link--current'  :pageNumber === page}" @click.prevent=" paginate(pageNumber)">
       {{ pageNumber}}
      </a>
    </li>

    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница"  @click.prevent="next(page)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>
<script>
export default{
  model:{
    prop: 'page',
    event: 'paginate'
  },
  props: ['page', 'count', 'prePage'],
  computed: {
    pages(){  return Math.ceil(this.count/this.prePage) }
  },
  methods:{
    paginate(page){ this.$emit('paginate', page)},
    next(page){ if( page>0 && page<this.pages){this.$emit('paginate', page + 1)}},
    prev(page){ if( page>1 && page<this.pages+1){this.$emit('paginate', page - 1)}},

 }
}
</script>
<style>
.pagination__link{
  cursor: pointer;
}
</style>
